@import "../../css-config/mixins.scss";

.category {
  margin: 30px 0;
  @include md {
    margin: 75px 0;
  }

  .layout {
    max-width: calc(100% - 20px);
    margin: 0 auto;
    padding: 0 10px;
    @include md {
      max-width: 1200px;
    }
  }

  .title {
    font-size: 24px;
    @include md {
      font-size: 34px;
    }
  }

  .products-container {
    margin: 20px 0;
    @include md {
      margin: 50px 0;
    }
  }
}
